import React,{Component} from 'react';
import {Container,Row,Col,Image} from 'react-bootstrap'
import BlackDiv from '../custom/BlackDiv'
class Portfolio extends Component{
    render(){
        return(
            <Container className="container-page-3">
                <h3>PORTFOLIO</h3>
                <hr/>
                <Row>
                    <Col xs={12} sm={6} className="column-portfolio">
                        <BlackDiv top={"10px"} right={"10px"} bottom={"10px"} left={"10px"}/>
                        <Image></Image>
                        <Col className="column-item">
                            <p>nazwa</p><i className="fab fa-github"></i>
                        </Col>
                    </Col>
                    <Col xs={12} sm={6} className="column-portfolio">
                        <BlackDiv top={"10px"} right={"10px"} bottom={"10px"} left={"10px"}/>
                        <Image></Image>
                        <Col className="column-item">
                            <p>nazwa</p><i className="fab fa-github"></i>
                        </Col>
                    </Col>
                    <Col xs={12} sm={6} className="column-portfolio">
                        <BlackDiv top={"10px"} right={"10px"} bottom={"10px"} left={"10px"}/>
                        <Image></Image>
                        <Col className="column-item">
                            <p>nazwa</p><i className="fab fa-github"></i>
                        </Col>
                    </Col>
                    <Col xs={12} sm={6} className="column-portfolio">
                        <BlackDiv top={"10px"} right={"10px"} bottom={"10px"} left={"10px"}/>
                        <Image></Image>
                        <Col className="column-item">
                            <p>nazwa</p><i className="fab fa-github"></i>
                        </Col>
                    </Col>
                    <Col xs={12} sm={6} className="column-portfolio">
                        <BlackDiv top={"10px"} right={"10px"} bottom={"10px"} left={"10px"}/>
                        <Image></Image>
                        <Col className="column-item">
                            <p>nazwa</p><i className="fab fa-github"></i>
                        </Col>
                    </Col>
                    <Col xs={12} sm={6} className="column-portfolio-arrow">
                        <BlackDiv top={"10px"} right={"10px"} bottom={"10px"} left={"10px"}/>
                        <i className="fas fa-arrow-left" onClick={() => this.props.handle("previous")}></i>
                    </Col>
                </Row>
            </Container>
        )
    }
}
export default Portfolio;